import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId || 0,
			HosGroupName:this.$route.query.HosGroupName || '',
			search:this.$route.query.search,
			from:this.$route.query.from,//1是专科,2是全部
			Goods:[],
			
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			GoodsNum:0,
			ListDetails:this.$store.state.preList,
			specsShow:false,
			carShow:false,
			spcData:null,
			chooseDrShow:false,
			page:1,
			haveMore: true,
			inputFlag: false,
			pageShow: false,
			showConfirm: false,
			act2:null,
			list:[],
			drSearch:'',
			act3:null,
			user: this.$store.state.userInfo,
			title:"",
			actT:null,
			SpecialId:null,
			idxNum:0,
			GoodsTypeNo:'',
			istHos:false,
			yEvent:null,
        }
    },
     computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails && this.ListDetails.length){
                this.ListDetails.forEach(v => {
    				if(v.GoodsSource == 1){
    					 p += (v.Price * v.GoodsNum)
    				}else{
    					p += (v.VipPrice * v.GoodsNum)
    				}
                })
            }
            return p.toFixed(2)
        },
    	totalM(){
    		let p = 0
    		if(this.ListDetails && this.ListDetails.length){
    		    this.ListDetails.forEach(v => {
    		        p += Number(v.RecommondAmount) * v.GoodsNum
    		    })
    		}
    		return p
    	}
    },
    created(){
        this.pullList()
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		goDetail(i){
			this.$router.push(`/preDetail?id=${i.GoodsId}`)
		},
		alertMsgevt(event){
			var $ball = this.$refs.ball;
			$ball.style.top = event.pageY+'px';
			$ball.style.left = event.pageX+'px';
			$ball.style.transition = 'left 0s, top 0s';
			setTimeout(()=>{
				$ball.style.top = (window.innerHeight-50)+'px';
				$ball.style.left = '40px';
				$ball.style.zIndex = 1000;
				$ball.style.transition = 'left 1s linear, top 1s ease-in';
			}, 20)
			setTimeout(()=>{
				$ball.style.display ='none';
				$ball.style.top = window.innerHeight+'px';
				$ball.style.display ='block';
			}, 1000)
		},
		 pullList(h){
		    if(this.haveMore){
				this.$post('PlatFormAPI/SpecialistHosGroup/GetMallGoodsByName',{
					DrId:this.user.Data.User.DoctorId,
				    GoodsName:this.search,
				    PageIndex: this.page,
				    PageSize: 15
				}).then(o => {
				    this.pageShow = true
				    if (!o.ReturnData || o.ReturnData.length < 15){
				        this.haveMore = false
				    }
				    else {
				        this.haveMore = true
				    }                
				    if (h){
				        o.ReturnData.forEach(v => {
				            this.Goods.push(v)
				        })
				    }
				    else {
				        this.Goods = o.ReturnData
				    }
					if(this.ListDetails && this.ListDetails.length){
						this.ListDetails.forEach((v,k) => {
						   o.ReturnData.forEach((i,y) => {
							   if(v.ServiceItemId == i.GoodsId){
								   o.ReturnData[y].GoodsNum = this.ListDetails[k].NumValue
								   this.ListDetails.splice(k,1,i)
							   }else{
								   if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId){
								   		o.ReturnData.splice(y, 1, v)
								   }
							   }
						   })
						})
						
					}
				})
		        
		    }
		},
		add(i,idx,d,$event){
			if(d){
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
						i.GoodsNum = v.GoodsNum + 1
						this.ListDetails.splice(k, 1, i)
					}
				})
				this.alertMsgevt($event)
			}else{
				this.yEvent = $event
				if(i.GoodsSpec && i.GoodsSpec.length > 1 && i.GoodsSource != 1){
					var t = 0
					this.ListDetails.forEach((v,k) => {
						i.GoodsSpec.forEach((a,y) => {
							if(v.GoodsSpecId == a.GoodsSpecId){
							   this.act2 = y
							   t = 1
							}
						})
					})
					if(t==0){
						this.act2 = null
					}
					this.spcData = i
					this.carShow = true
					return
				}else if(i.GoodsSource == 1){
					this.drSearch = ""
					this.spcData = i
					this.chooseDrShow = true
					this.pull()
					return
				}else if(i.GoodsSource == 4){
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}else{
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								i.GoodsSpecId = v.GoodsSpecId
								i.GoodsSpecName = v.GoodsSpecName
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
							i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
						i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}
			}
		
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		addspc(a){
			if(a.GoodsNum){
				this.spcData.GoodsNum = a.GoodsNum + 1
			}else{
				this.spcData.GoodsNum = 1
			}
			this.carShow = false
			this.carShow = true
		},
		reducespc(a){
			if(this.spcData.GoodsNum <= 1){
				return 
			}
			this.spcData.GoodsNum = a.GoodsNum - 1
			this.carShow = false
			this.carShow = true
		},
		sSpecName(i,idx){
			this.act2 = idx
			this.spcData.GoodsSpecId = i.GoodsSpecId
			this.spcData.GoodsSpecName = i.GoodsSpecName
			this.spcData.VipPrice = i.VipPrice
		},
		carSure(a){
			if(!a.GoodsSpecId){
				return this.$toast('请选择规格')
			}
			if(!this.spcData.GoodsNum){
				return this.$toast('请输入数量')
			}
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = this.spcData.GoodsNum
					this.ListDetails.push(a)
				}
			}else{
				a.GoodsNum = this.spcData.GoodsNum
				this.ListDetails.push(a)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		reduce(i,n){
			this.ListDetails.forEach((v,k) => {
			    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){ 
					i.GoodsNum = v.GoodsNum - 1
					if(i.GoodsNum < 1){
						this.ListDetails.splice(k, 1)
					}else{
						this.ListDetails.splice(k, 1, i)
					}
					if(!this.ListDetails.length){
						this.specsShow = false
					}
				}
			})
			console.log(this.ListDetails)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		specsCar(){
			if(this.specsShow){
				this.specsShow = false
			}else{
				this.specsShow = true
			}
		},
		del(){
			this.specsShow = false
			this.ListDetails = []
			this.Goods.forEach((v,k) => {
			   v.GoodsNum = 0
			})
			this.$store.dispatch('pushpreList',[])
		},
		closeCar(a){
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = 0
				}
			}else{
				a.GoodsNum = 0
			}
		},
		sendSave(){
			this.ListDetails.forEach((v,k) => {
			    if(!v.ServiceItemId){  
					this.ListDetails[k].ServiceItemId = v.GoodsId
					this.ListDetails[k].ServiceItemName = v.GoodsName
					this.ListDetails[k].Price = v.VipPrice
					this.ListDetails[k].MasterPicture = v.ImgPath
					this.ListDetails[k].NumValue = v.GoodsNum
					this.ListDetails[k].isChecked = 1
				}
			})
			this.$store.dispatch('pushpreList',this.ListDetails)
			this.$router.replace('/create-sevice')
			
		},
		
        searchFor(){
			this.page = 1
			this.haveMore = true
			this.pullList()
        },

        getMore(){
            const el = this.$refs.el
            let timer = null
            el.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
							const top = el.scrollTop ||
								el.pageYOffset ||
								el.scrollTop,
								h = el.scrollHeight,
								wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pullList(true)
                    }
                }, 100)
            }
        },

    }
}



